*,
*:after,
*:before {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

html {
  font-size: 62.5%;
  height: 100vh;
  background-image: -webkit-linear-gradient(69deg, #28283e, #28283e 40%, #2b2b44 40%);
  background-image: -o-linear-gradient(69deg, #28283e, #28283e 40%, #2b2b44 40%);
  background-image: linear-gradient(21deg, #28283e, #28283e 40%, #2b2b44 40%); }

body {
  font-size: 1.6rem;
  font-family: 'Titillium Web', sans-serif;
  text-align: center;
  line-height: 1.2;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center; }

.logo {
  width: 10rem; }

h1 {
  font-size: 6rem;
  color: #eee;
  letter-spacing: 1rem;
  font-weight: normal; }

h3 {
  color: #f8126a;
  font-size: 2rem;
  letter-spacing: 2px;
  font-weight: normal; }

@media screen and (max-width: 1024px) {
  html {
    font-size: 55%; } }

@media screen and (max-width: 800px) {
  html {
    font-size: 50%; } }

@media screen and (max-width: 414px) {
  html {
    font-size: 45%; } }



